@import '../../UI/styles/styles.scss';

.basketItem {
  display: flex;
  font-weight: bold;
  justify-content: space-between;
  align-items: center;
  padding: 30px;
  margin-top: 20px;
  font-size: 25px;
  .basketImg {
    width: 150px;
    filter: $dropShadowIng;
  }
  button {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background: $red;
    color: $white;
    outline: none;
    border: none;
    transition: .3s;
    cursor: pointer;
    font-size: 20px;
    &:hover {
      transform: scale(1.2);
      transition: .3s;
    }
  }
}
